.head_box {
  padding: 2rem 0;
  width: 60rem;
}

.section_outer {
  position: relative;
  top: 6rem;
}
.search_text {
  /* padding: 2rem 0 0; */
  font-size: 2.5rem;
  color: black;
}
.search_text > span {
  color: #f89e86;
}
.search_box,
.filter_box {
  margin: 1rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search,
select,
.reset,
.filter_search {
  height: 4rem;
  padding: 0 0 0 15px;
  border-radius: 8px;
  font-size: 16px;
  color: #00000a;
  width: 90%;
  border: solid 1px #00000a;
  white-space: pre-wrap;
}
.search_icon {
  width: 4rem;
  height: 4rem;
  border: solid 1px #00000a;
  background-color: white;
}
.search_icon > svg {
  fill: #00000a;
}
.select_box {
  display: contents;
}
.select_box_fetival {
  width: 22rem;
}
.select_box_show {
  width: 14.5rem;
}
select {
  width: 100%;
}
.reset {
  padding: 0;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  background-color: white;
}
.reset > svg {
  fill: #00000a;
  width: 2rem;
}
.filter_search {
  padding: 0;
  width: 10rem;
  background-color: white;
}

.order_box {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.order {
  width: fit-content;
  margin: 0 0.5rem;
  font-size: 1rem;
  border: none;
}
.check {
  color: #f89e86;
  font-weight: bold;
}

.thumbnail_box {
  height: 18rem;
  width: 18rem;
  overflow: hidden;
  position: relative;
}
.ing {
  z-index: 1;
  display: block;
  position: absolute;
  top: 8px;
  left: 8px;
  width: 3.5rem;
  height: 2rem;
  background-color: #1b263b;
  border-radius: 4px;
  isolation: isolate;
  font-size: 12px;
  font-weight: 600;
  line-height: 32px;
  color: #f89e86;
  text-align: center;
  border: solid #f89e86 1px;
}
.thumbnail {
  display: block;
  width: 100%;
  height: 100%;
  isolation: isolate;
  object-fit: cover;
  border-radius: 8px;
}
.list_box {
  display: flex;
  max-width: 60rem;
  text-align: initial;
  padding: 0;
  border: none;
  border-radius: 0;
  flex-wrap: wrap;
  margin: 0;
}
.list {
  padding: 1rem;
  box-sizing: border-box;
}
.content_box {
  width: 18rem;
}
.list_name {
  font-size: 1.3rem;
  padding: 0.7rem 0.4rem 0;
  font-weight: bold;
}
.list_date {
  font-size: 1rem;
  padding: 0.5rem 0.4rem 0;
  /* color: #1a1a24; */
}
.list_area {
  font-size: 0.9rem;
  padding: 0.2rem 0.4rem;
  /* color: #666670; */
}
a {
  text-decoration: none;
}
.list > a {
  color: black;
}
.list > a:visited {
  color: black;
}
.list:hover {
  background-color: #1b263b;
  border-radius: 10px;
}
.list:hover > a > div.content_box > div.list_name {
  color: white;
}
.list:hover > a {
  color: #f89e86;
}
